/**
 * MindShore Project Catalog - Data Schema
 * Defines TypeScript types for project data structure
 */
/**
 * Data validation helpers
 */
export const PROJECT_CATEGORIES = ['Data & AI', 'Software Engineering', 'ERP/CRM'];
export const SORT_OPTIONS = ['newest', 'az'];
/**
 * Type guards for runtime validation
 */
export function isValidProject(data) {
    return (typeof data === 'object' &&
        typeof data.id === 'string' &&
        typeof data.title === 'string' &&
        typeof data.client === 'string' &&
        typeof data.category === 'string' &&
        typeof data.summary === 'string' &&
        Array.isArray(data.problem) &&
        Array.isArray(data.approach) &&
        Array.isArray(data.outcomes) &&
        Array.isArray(data.techStack) &&
        Array.isArray(data.tags) &&
        Array.isArray(data.images));
}
export function isValidProjectImage(data) {
    return (typeof data === 'object' &&
        typeof data.src === 'string' &&
        typeof data.alt === 'string');
}
/**
 * Default values and constants
 */
