/**
 * Search and filtering utilities for MindShore Project Catalog
 */
/**
 * Filters and sorts projects based on current filter state
 */
export function filterProjects(projects, filters) {
    let filtered = [...projects];
    // Filter by category
    if (filters.category && filters.category !== 'All') {
        filtered = filtered.filter(project => project.category === filters.category);
    }
    // Filter by search query
    if (filters.query.trim()) {
        const query = filters.query.toLowerCase().trim();
        filtered = filtered.filter(project => searchInProject(project, query));
    }
    // Sort results
    return sortProjects(filtered, filters.sortBy);
}
/**
 * Searches within a single project for the query term
 */
function searchInProject(project, query) {
    const searchFields = [
        project.title,
        project.client,
        project.summary,
        project.category,
        ...project.problem,
        ...project.approach,
        ...project.outcomes,
        ...project.techStack,
        ...project.tags
    ];
    return searchFields.some(field => field.toLowerCase().includes(query));
}
/**
 * Sorts projects based on specified criteria
 */
function sortProjects(projects, sortBy) {
    return [...projects].sort((a, b) => {
        switch (sortBy) {
            case 'newest':
                // Sort by date (newest first), fallback to title
                const dateA = a.date ? new Date(a.date).getTime() : 0;
                const dateB = b.date ? new Date(b.date).getTime() : 0;
                if (dateA !== dateB) {
                    return dateB - dateA; // Newest first
                }
                return a.title.localeCompare(b.title);
            case 'az':
                return a.title.localeCompare(b.title);
            default:
                return 0;
        }
    });
}
/**
 * Extracts unique categories from projects
 */
export function getUniqueCategories(projects) {
    const categories = new Set(projects.map(p => p.category));
    return ['All', ...Array.from(categories).sort()];
}
/**
 * Extracts unique tech stack items from projects
 */
export function getUniqueTechStack(projects) {
    const techItems = new Set();
    projects.forEach(project => {
        project.techStack.forEach(tech => techItems.add(tech));
    });
    return Array.from(techItems).sort();
}
/**
 * Extracts unique tags from projects
 */
export function getUniqueTags(projects) {
    const tags = new Set();
    projects.forEach(project => {
        project.tags.forEach(tag => tags.add(tag));
    });
    return Array.from(tags).sort();
}
/**
 * Searches for similar projects based on tags and tech stack
 */
export function findSimilarProjects(targetProject, allProjects, limit = 3) {
    if (!targetProject)
        return [];
    const candidates = allProjects.filter(p => p.id !== targetProject.id);
    // Score projects based on similarity
    const scored = candidates.map(project => ({
        project,
        score: calculateSimilarityScore(targetProject, project)
    }));
    // Sort by score and return top matches
    return scored
        .sort((a, b) => b.score - a.score)
        .slice(0, limit)
        .map(item => item.project);
}
/**
 * Calculates similarity score between two projects
 */
function calculateSimilarityScore(projectA, projectB) {
    let score = 0;
    // Category match (high weight)
    if (projectA.category === projectB.category) {
        score += 10;
    }
    // Tech stack overlap
    const techOverlap = projectA.techStack.filter(tech => projectB.techStack.includes(tech)).length;
    score += techOverlap * 2;
    // Tag overlap
    const tagOverlap = projectA.tags.filter(tag => projectB.tags.includes(tag)).length;
    score += tagOverlap * 1.5;
    // Client industry similarity (basic heuristic)
    if (projectA.client.split(' ')[0] === projectB.client.split(' ')[0]) {
        score += 3;
    }
    return score;
}
/**
 * Highlights search query in text
 */
export function highlightSearchQuery(text, query) {
    if (!query.trim())
        return text;
    const regex = new RegExp(`(${escapeRegExp(query)})`, 'gi');
    return text.replace(regex, '<mark class="bg-brand-primary/30 text-brand-primary">$1</mark>');
}
/**
 * Escapes special regex characters
 */
function escapeRegExp(string) {
    return string.replace(/[.*+?^${}()|[\]\\]/g, '\\$&');
}
/**
 * Debounces function calls
 */
export function debounce(func, wait) {
    let timeout;
    return (...args) => {
        clearTimeout(timeout);
        timeout = setTimeout(() => func(...args), wait);
    };
}
/**
 * Generates search suggestions based on project data
 */
export function generateSearchSuggestions(projects, query, limit = 5) {
    if (!query.trim())
        return [];
    const suggestions = new Set();
    const lowerQuery = query.toLowerCase();
    // Collect potential matches
    projects.forEach(project => {
        // Add matching tech stack items
        project.techStack.forEach(tech => {
            if (tech.toLowerCase().includes(lowerQuery)) {
                suggestions.add(tech);
            }
        });
        // Add matching tags
        project.tags.forEach(tag => {
            if (tag.toLowerCase().includes(lowerQuery)) {
                suggestions.add(tag);
            }
        });
        // Add matching words from titles
        const titleWords = project.title.split(' ');
        titleWords.forEach(word => {
            if (word.toLowerCase().includes(lowerQuery) && word.length > 2) {
                suggestions.add(word);
            }
        });
    });
    return Array.from(suggestions)
        .sort((a, b) => {
        // Prioritize exact matches
        const aExact = a.toLowerCase() === lowerQuery;
        const bExact = b.toLowerCase() === lowerQuery;
        if (aExact && !bExact)
            return -1;
        if (bExact && !aExact)
            return 1;
        // Then by how early the query appears
        const aIndex = a.toLowerCase().indexOf(lowerQuery);
        const bIndex = b.toLowerCase().indexOf(lowerQuery);
        if (aIndex !== bIndex)
            return aIndex - bIndex;
        // Finally by length
        return a.length - b.length;
    })
        .slice(0, limit);
}
